:root {
    --background-color: #1f1f1f;
    --light-background-color: #333333;
    --highlight-background-color: #3f3f3f;

    --content-height: 80%;
    --settings-height: 20%;

    --time-panel-width: 60%;
    --weather-panel-width: 40%;
}

html, body, div#root{
    margin: 0;
    background-color: var(--background-color);
    height: 100%;
    width: 100%;
    font-family: Arial, Helvetica, sans-serif;
}
/* container for content and settings */
.home {}

.content {
    position: relative;
    height: var(--content-height);
    width: 100%;
}
.content-column {
    float: left;
}
.content-column#time {
    width: var(--time-panel-width);
}
.content-column#weather {
    width: var(--weather-panel-width);
}
.content:after {
    content: "";
    display: block;
    clear: both;
}
@media screen and (max-width: 800px) {
    .content-column#time, .content-column#weather{
      width: 100%;
    }
}
.bottom-panel {
    height: var(--settings-height);
    width: 100%;
    margin-bottom: 25px;
}
.copyright {
    position: fixed;
    bottom: 0;

    width: 100%;

    background-color: var(--light-background-color);
    
    color: hsl(0, 0%, 58%);
    font-family: 'Courier New', Courier, monospace;
    font-size: 15px;
    text-align: center;
}